.Pages {
  text-align: center;
  background: #F5F5F5;
  width: 100%;
  height: 100vh;
}

.Pages-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@font-face {
  font-family: 'MyFont';
  src: local('MyFont'), url(/assets/fonts/Roboto-Medium.ttf) format('ttf');
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

li {
  font-size: 25px;
}